* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

:root {
  --nav-color: #ffd269;
  --img-size: 200px;
}

#no-overflow {
  overflow-y: hidden;
}

.none {
  display: none;
}

nav {
  background-color: var(--nav-color);
  padding: 20px;
}

#ingredients-div {
  background-color: #fff;
  flex-flow: wrap;
  gap: 10px;
  width: 50%;
  max-height: 300px;
  padding: 10px;
  display: flex;
  overflow-y: scroll;
}

.ingredient {
  cursor: pointer;
  background-color: var(--nav-color);
  text-transform: capitalize;
  border-radius: 10px;
  padding: 10px;
  display: inline;
}

.ingredient.selected {
  background-color: #d69600;
}

#loading {
  z-index: 5;
  background-color: #fff;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
}

#data {
  flex-flow: wrap;
  justify-content: center;
  align-items: flex-start;
  gap: 2rem;
  width: 80%;
  margin: 5rem auto;
  display: flex;
}

#data img {
  width: var(--img-size);
  height: var(--img-size);
  aspect-ratio: 16 / 9;
  object-fit: contain;
  margin-bottom: 10px;
}

#data h1 {
  width: var(--img-size);
  text-align: center;
  overflow-wrap: word-wrap;
  font-size: 1.7rem;
}

#data p {
  text-align: center;
  font-size: .8rem;
}

.coctail {
  cursor: pointer;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 5px;
  display: inline-flex;
}

#data.none {
  display: none;
}

#data div:hover > img {
  opacity: .7;
}
